


[class*="entypo-"]:before {
    font-family: 'entypo', sans-serif;
}

body {
    /* background: #fffcdd; */
    padding: 1em;
}

.title-pen {
    color: #333;
    font-family: "Coda", sans-serif;
    text-align: center;
}

.title-pen span {
    color: #55acee;
}

.user-profile {
    margin: auto;
    width: 25em;
    /* height: 11em; */
    background: #fff;
    border-radius: .3em;
    padding: 1rem;
}

.user-profile .username {
    margin: auto;
    margin-top: -4.40em;
    padding: rem;
    margin-left: 5.80em;
    color: #658585;
    font-size: 1.53em;
    font-family: "Coda", sans-serif;
    font-weight: bold;
}

.user-profile .bio {
    margin: auto;
    display: inline-block;
    margin-left: 10.43em;
    color: #e76043;
    font-size: .87em;
    font-family: "varela round", sans-serif;
}

.user-profile>.description {
    margin: auto;
    margin-top: 1.35em;
    margin-right: 4.43em;
    width: 200em;
    color: #c0c5c5;
    font-size: .87em;
    font-family: "varela round", sans-serif;
}

.user-profile>img.avatar {
    padding: .7em;
    margin-left: .3em;
    margin-top: .3em;
    height: 8.23em;
    width: 7.23em;
    border-radius: 5em;
}

.user-profile ul.data {
    margin: 2em auto;
    height: 4.70em;
    /* width: 30rem; */
    background: #388484;
    text-align: center;
    border-radius: 0 0 .3em .3em;
    font-size:.75rem;
}

.user-profile li {
    margin: 0 auto;
    padding: 1.30em;
    width: 33.33334%;
    display: table-cell;
    text-align: center;
}

.user-profile span {
    font-family: "varela round", sans-serif;
    color: #e3eeee;
    white-space: nowrap;
    font-size: 1.27em;
    font-weight: bold;
}

.user-profile span:hover {
    color: #daebea;
}
.data .Donate{
    text-align:center ;
  padding: 2rem;

}
